.recipeContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 350px;
  height: 600px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.5);
  margin: 15px;
  padding: 10px 20px;
}

.image {
  height: 150px;
  width: 150px;
  border-radius: 50%;
}
